.msg {
  font-size: 0.7em;
  text-align: left;
  border: 1px solid #f9f9f9;
  border-radius: 5px;
  padding: 1.5em;
  margin: 1em;

  position: relative;
  word-wrap: break-word;
  max-height: 100px;
  white-space: pre-wrap;
}

.room-msg {
  background-color: #efeded;
}


.room-msg::before {
  content: '';
  border-top: solid -4px transparent;
  border-right: solid 20px #efeded;
  border-bottom: solid 28px transparent;
  position: absolute;
  left: -12px;
  width: 0;
  height: 0;
  top: 8px;
}

.my-msg {
  background-color: #fed2ff;
  justify-self: end;  
}

.my-msg::after {
  content: '';
  border-top: solid -4px transparent;
  border-left: solid 20px #fed2ff;
  border-bottom: solid 28px transparent;
  position: absolute;
  right: -12px;
  width: 0;
  height: 0;
  top: 8px;
}